<template>
<div class="fade-in">

  <h1 class="page-header">Lists</h1>
  <div id="newBtn_container">
    <router-link to="/"><i class="fas fa-home"></i></router-link>
    <button id="newBtn" @click="add = !add"><i class="fas fa-plus"></i></button>
  </div>

  <!-- Create new list -->
  <div id="createNewListItem" v-if="add === true">
    <form class="new_list_form form-group col-md-12">
      <label for="name">List name</label>
      <input type="text" name="name" class="form-control mb-2" v-model="list.name" maxlength="25" placeholder="max length 25">
      <label for="list_item">Add item to {{ list.name }}</label>
      <div class="" style="display:flex; justify-content:space-between; flex-flow:column">
        <input type="text" class="form-control mb-2" v-model="list_item" placeholder="add to list">
        <button id="add_list_item_btn" @click.prevent="addListItem"><i class="fas fa-plus"></i></button>
        <div style="margin:15px auto;">
          <h6 v-if="newListItems.length > 0">Current List Items:</h6>
          <ul>
            <li v-for="item in newListItems" :key="item.id"><h6 class="new_item">{{ item }}</h6></li>
          </ul>
          <h5 v-if="feedback">{{ feedback }}</h5>
        </div>
      </div>
      <div id="btn_div" class="col-sm-12">
        <button class="btn btn-success mt-2" @click.prevent="save_list">Save</button>
        <button class="btn btn-cancel mt-2" @click.prevent="add = false">Cancel</button>
      </div>
    </form>
  </div>

  <!-- View Lists -->
  <div class="list-list" v-else>
    <ul>
      <li v-for="list in lists" :key="list.id">
        <div class="list">
          <div class="main_list_section scale-in-center">
            <h6 class="list_item_name" @click="viewList(list.id)">{{ list.name }}</h6>
            <div class="list_btn">
              <button @click="closeList(list.id)" style="margin-right:10px; margin-left:auto;"><i class="fas fa-minus"></i></button>
              <button @click="viewList(list.id)" style="margin-right:10px; margin-left:auto;"><i class="fas fa-folder-open"></i></button>
              <button @click="removeList(list.id)"><i class="fas fa-trash icon-trash"></i></button>
            </div>
          </div>
          <!-- Display selected list - list items -->
          <div class="list_items_view" :id='list.id'>
            <div class="add_list_item">
              <input type="text" placeholder="add new list item" v-show="newItemCheck" v-model="newItem" id="new_list_input">
              <i class="fas fa-plus" style="float:right;" @click="add_to_list(list.id)"/>
            </div>
            <ul>
              <li v-for="item in list.items" :key="item"  class="listItems slide-in-blurred-right">
                <div class="single_list_item">
                  <p class="list_item" style="margin:0;">{{ item }}</p>
                  <button @click="removeListItem(list.id, item)" class="remove_single_item"><i class="fas fa-minus"></i></button>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</div>
</template>
<script>
import firebase from "firebase/app";
import { db } from "@/config/db.js";
import _ from "lodash";

export default {
  name: "lists",
  /*props: {
    id: {
      required: true
    }
  }, */
  data() {
    return {
      list: {
        name: "",
        item: []
      },
      add: false,
      feedback: "",
      newItemCheck: false,
      newItem: "",
      lists: [],
      listItems: [],
      newListItems: [],
      list_item: "",
      view: false,
      previousList: ""
    };
  },
  methods: {
    save_list() {
      // Create and save a new list
      if (this.list.name) {
        if (
          !localStorage.getItem("myorganiser_userId") ||
          localStorage.getItem("myorganiser_userId") === ""
        ) {
          this.$router.push("/"); // Check for active user
        }
        // Save list to firebase
        db
          .collection("Lists")
          .add({
            name:
              this.list.name.charAt(0).toUpperCase() + this.list.name.slice(1),
            items: this.newListItems,
            user_id: localStorage.getItem("myorganiser_userId")
          })
          .then(() => {
            this.list.name = "";
            this.listItems = [];
            this.newListItems = [];
            this.add = false;
            this.feedback = "";
          })
          .catch(err => {
            this.feedback = `Error, could not save list: ${err.message}`;
          });
      }
    },
    cancel_add_list() {
      // Cancel creation of new list
      this.add = false;
    },
    viewList(id) {
      // View a single list
      if (this.previousList) {
        document.getElementById(this.previousList).style.display = "none";
      }
      this.listItems = db.collection("Lists").doc(id);
      document.getElementById(id).style.display = "block";
      this.previousList = id;
    },
    removeList(id) {
      // Remove a list
      db
        .collection("Lists")
        .doc(id)
        .delete();
    },
    closeList(id) {
      // Close list view
      document.getElementById(id).style.display = "none";
    },
    addListItem() {
      // Add initial list item(s) on new list creation
      if (this.list_item) {
        this.newListItems.push(this.list_item);
      }
      this.list_item = "";
    },

    add_to_list(id) {
      // Add item to existing list of items in existing List
      if (!this.newItemCheck) {
        this.newItemCheck = true;
        return;
      }
      document.getElementById("new_list_input").focus();
      // Save item to firebase
      if (this.newItem) {
        db
          .collection("Lists")
          .doc(id)
          .update({
            items: firebase.firestore.FieldValue.arrayUnion(this.newItem)
          })
          .then(() => {
            this.newItem = "";
            document.getElementById("new_list_input").focus();
          })
          .catch(err => {
            console.log("Update Error: " + err.message);
          });
      }
    },
    removeListItem(id, item) {
      db
        .collection("Lists")
        .doc(id)
        .update({ items: firebase.firestore.FieldValue.arrayRemove(item) });
    }
  },
  firestore() {
    return {
      lists: db
        .collection("Lists")
        .where("user_id", "==", localStorage.getItem("myorganiser_userId"))
    };
  },
  beforeMount() {
    if (!this.$store.getters.getLoggedStatus) {
      this.$router.push("/");
    }
  },
  computed: {
    sortedLists() {
      return _.orderBy(this.lists, "name");
    }
  }
};
</script>
